import { combineReducers } from "redux";
import storage from 'redux-persist/lib/storage'
import AuthReducer from "./auth_reducer";
// import CreateReducer from "./createReducers"
import ListReducer from "./list_reducer";
import UpdateReducer from "./update_reducer";
import { USER_LOGGED_OUT } from "../actions/types"


const appReducer = combineReducers({
    auth_reducer: AuthReducer,
    // create_reducer:CreateReducer,
    list_reducer: ListReducer,
    update_reducer: UpdateReducer,
})

const rootReducer = (state, action) => {
    if (action.type === USER_LOGGED_OUT) {
        // for all keys defined in your persistConfig(s)
        storage.removeItem('persist:root')
        // storage.removeItem('persist:otherKey')

        state = undefined;
    }
    return appReducer(state, action);
};

export default rootReducer