import {
    ADD_CUSTOMER,

    UPDATE_DASHBOARD_METRIC, UPDATE_SELECTION_DATES, UPDATE_CURRENCY_TYPE, UPDATE_PRODUCT_MONTH_YEAR,
    UPDATE_DEPOT_MONTH_MONTH, UPDATE_DEPOT_MONTH_YEAR, UPDATE_DEPOT_PRODUCT_MONTH_MONTH, UPDATE_DEPOT_PRODUCT_MONTH_YEAR,
    UPDATE_DEPOT, UPDATE_DEPOT_CUSTOMER_MONTH, UPDATE_DEPOT_CUSTOMER_YEAR,

    UPDATE_TOP_CUSTOMER_MONTH, UPDATE_TOP_CUSTOMER_PRODUCT, UPDATE_TOP_CUSTOMER_YEAR,
    UPDATE_SALES_DATE, UPDATE_CUSTOMER_YEAR,

    UPLOAD_EXCEL, UPLOAD_EXCEL_ERROR,
} from "../actions/types"

const merge = (prev, next) => Object.assign({}, prev, next)

const UPDATE_REDUCER = (state = {}, action) => {

    switch (action.type) {
        // Get token
        case ADD_CUSTOMER:
            return merge(state, { add_customer: action.payload })
        case UPDATE_DASHBOARD_METRIC:
            return merge(state, { dashboard_metric: action.payload })
        case UPDATE_CUSTOMER_YEAR:
            return merge(state, { customer_year: action.payload })
        case UPDATE_SELECTION_DATES:
            return merge(state, { selection_dates: action.payload })
        case UPDATE_CURRENCY_TYPE:
            return merge(state, { currency_type: action.payload })
        case UPDATE_PRODUCT_MONTH_YEAR:
            return merge(state, { product_month_year: action.payload })

        case UPDATE_DEPOT_MONTH_MONTH:
            return merge(state, { depot_month_month: action.payload })
        case UPDATE_DEPOT_MONTH_YEAR:
            return merge(state, { depot_month_year: action.payload })
        case UPDATE_DEPOT_PRODUCT_MONTH_YEAR:
            return merge(state, { depot_product_month_year: action.payload })
        case UPDATE_DEPOT_PRODUCT_MONTH_MONTH:
            return merge(state, { depot_product_month_month: action.payload })
        case UPDATE_DEPOT:
            return merge(state, { depot: action.payload })
        case UPDATE_DEPOT_CUSTOMER_MONTH:
            return merge(state, { depot_customer_month: action.payload })
        case UPDATE_DEPOT_CUSTOMER_YEAR:
            return merge(state, { depot_customer_year: action.payload })


        case UPDATE_TOP_CUSTOMER_MONTH:
            return merge(state, { top_customer_month: action.payload })

        case UPDATE_TOP_CUSTOMER_YEAR:
            return merge(state, { top_customer_year: action.payload })

        case UPDATE_TOP_CUSTOMER_PRODUCT:
            return merge(state, { top_customer_product: action.payload })

        case UPDATE_SALES_DATE:
            return merge(state, { sales_date: action.payload })
        case UPLOAD_EXCEL:

            return merge(state, { uploaded_excel: action.payload, upload_excel_error: null })

        case UPLOAD_EXCEL_ERROR:

            return merge(state, { upload_excel_error: action.payload })

        default:
            return state
    }
}

export default UPDATE_REDUCER