import React, { lazy, Suspense } from "react";
import { BrowserRouter as Router, Routes, Route } from "react-router-dom";
import SpinnerComponent from "../components/utils/spinner";


// import LoginPage from "../components/auth";
// import DepotPage from "../components/depot";
// import HomePage from "../components/home";
// import SalesPage from "../components/sales";
// import CustomerPage from "../components/customer";
// import DownloadExcelReport from "../components/orders/excel"

const LoginPage = lazy(() => import('../components/auth'));
const HomePage = lazy(() => import('../components/home'))
const DepotPage = lazy(() => import('../components/depot'))
const SalesPage = lazy(() => import('../components/sales'))
const CustomerPage = lazy(() => import('../components/customer'))

class RoutesComponent extends React.Component {
    render() {
        return (
            <Router>
                <Suspense fallback={<SpinnerComponent spinner={true} />}>
                    <Routes>
                        <Route path="/login" exact element={<LoginPage />} />
                        <Route path="/" exact element={<HomePage />} />
                        <Route path="/sale" exact element={<SalesPage />} />
                        <Route path="/depots" exact element={<DepotPage />} />
                        <Route path="/customers" exact element={<CustomerPage />} />
                        {/* <Route path="/download_excel" exact component={DownloadExcelReport} /> */}
                    </Routes>
                </Suspense>
            </Router>
        )
    }
}

export default RoutesComponent;