import {
    GET_TOP_CUSTOMER, GET_CUSTOMER_LIST, GET_CUSTOMER_DETAIL, UPDATE_TOP_CUSTOMER, GET_TRUCK_LIST, UPDATE_GET_TRUCK_LIST,
    UPDATE_GET_CUSTOMER_LIST, TRUCK_CREATE, UPDATE_TRUCK_LIST,
    GET_DEPOT_MONTH, GET_DEPOT_PRODUCT_MONTH, GET_DEPOT_LIST, GET_DEPOT_SERIES, GET_DEPOT_PRODUCT_SERIES, GET_DEPOT_CUSTOMER,
    UPDATE_DEPOT_MONTH, UPDATE_DEPOT_PRODUCT_MONTH, UPDATE_DEPOT_SERIES, UPDATE_DEPOT_CUSTOMER,
    GET_PRODUCT_SERIES, GET_PRODUCT_MONTH, GET_PRODUCT_TOP_CUSTOMER, GET_PRODUCT_LIST, UPDATE_PRODUCT_SERIES,
    UPDATE_PRODUCT_MONTH,

    GET_SALES_LIST, UPDATE_GET_SALES_LIST, SALES_CREATE_ERROR,
} from "../actions/types"

const merge = (prev, next) => Object.assign({}, prev, next)

const LIST_REDUCER = (state = {}, action) => {

    switch (action.type) {

        case GET_TOP_CUSTOMER:
            return merge(state, { top_customer: action.payload, update_top_customer: false })
        case UPDATE_TOP_CUSTOMER:
            return merge(state, { update_top_customer: action.payload })
        case GET_CUSTOMER_LIST:
            return merge(state, { customer_list: action.payload, update_get_truck_list: false })
        case UPDATE_GET_CUSTOMER_LIST:
            return merge(state, { customer_list: action.payload, update_get_truck_list: false })
        case GET_TRUCK_LIST:
            return merge(state, { truck_list: action.payload, update_get_truck_list: false })
        case TRUCK_CREATE:
            return merge(state, { truck_create: action.payload })
        case UPDATE_TRUCK_LIST:
            return merge(state, { truck_list: action.payload, update_get_truck_list: false })
        case UPDATE_GET_TRUCK_LIST:
            return merge(state, { update_get_truck_list: true })
        case GET_CUSTOMER_DETAIL:
            return merge(state, { customer_detail: action.payload })

        case GET_DEPOT_LIST:
            return merge(state, { depot_list: action.payload })

        case GET_DEPOT_MONTH:
            return merge(state, { depot_month: action.payload, update_depot_month: false })

        case UPDATE_DEPOT_MONTH:
            return merge(state, { update_depot_month: true })

        case GET_DEPOT_PRODUCT_SERIES:
            return merge(state, { depot_product_series: action.payload, update_product_series: false })

        case GET_DEPOT_CUSTOMER:
            return merge(state, { depot_customer: action.payload, update_depot_customer: false })

        case UPDATE_DEPOT_CUSTOMER:
            return merge(state, { update_depot_customer: true })

        case GET_DEPOT_PRODUCT_MONTH:
            return merge(state, { depot_product_month: action.payload, update_depot_product_month: false })

        case UPDATE_DEPOT_PRODUCT_MONTH:
            return merge(state, { update_depot_product_month: true })

        case GET_DEPOT_SERIES:
            return merge(state, { depot_series: action.payload, update_product_series: false, update_depot_series: false })

        case UPDATE_DEPOT_SERIES:
            return merge(state, { update_depot_series: true })

        case GET_PRODUCT_SERIES:
            return merge(state, { product_series: action.payload, update_product_series: false })
        case UPDATE_PRODUCT_SERIES:
            return merge(state, { update_product_series: true })
        case GET_PRODUCT_MONTH:
            return merge(state, { product_month: action.payload, update_product_month: false })
        case UPDATE_PRODUCT_MONTH:
            return merge(state, { update_product_month: true })

        case GET_PRODUCT_TOP_CUSTOMER:
            return merge(state, { product_top_customer: action.payload, update_top_customer: false })
        case GET_PRODUCT_LIST:
            return merge(state, { product_list: action.payload })

        case GET_SALES_LIST:
            return merge(state, { sales_list: action.payload, update_get_sales_list: false, sales_create_error: null })

        case SALES_CREATE_ERROR:
            return merge(state, { sales_create_error: action.payload })
        case UPDATE_GET_SALES_LIST:
            return merge(state, { update_get_sales_list: true })

        default:
            return state
    }
}

export default LIST_REDUCER